import React, { useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Gallery from '@vkontakte/vkui/dist/components/Gallery/Gallery';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Arrow from '../img/arrow.svg';
import './Style.css';

const Intro = props => {

const firstOpenApp = () => {


    bridge.send("VKWebAppStorageSet", { "key": "hasSeenOnboarding7", "value": 'true' });
    bridge.send("VKWebAppStorageSet", { "key": "rating", "value": '0 ' });
    bridge.send("VKWebAppStorageSet", { "key": "geo", "value": '' });
    bridge.send("VKWebAppStorageSet", { "key": "pos", "value": '' });
    
    props.goToHome()
}

    return (

        <Panel id={props.id} separator={false}>
            <div className="stage">
                <ul class="circles">
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                </ul>
                <Gallery 
                    slideWidth="100%"
                    style={{ height: "90%" }}
                    bullets="dark"
                >
                    <div style={{ position: 'relative', marginTop: '20vh' }}>
                        <Div style={{ textAlign: 'center', fontSize: 18, fontFamily: 'Neucha' }}><h1>       Towner!</h1>
                            <img className="scale" style={{ position: 'absolute', top: '33%', marginLeft: '20%' }} src={Arrow} alt="" /></Div></div>
                    <div style={{ position: 'relative', marginTop: '20vh' }}>
                        <Div style={{ textAlign: 'center', fontSize: 18, fontFamily: 'Neucha' }}><h1>
                              ,           !
                        </h1><img className="scale" src={Arrow} alt="" style={{ position: 'absolute', top: '33%', marginLeft: '20%' }}/></Div></div>
                   
                    <div style={{ position: 'relative', marginTop: '20vh' }}>
                        <Div style={{ textAlign: 'center', fontSize: 18, fontFamily: 'Neucha' }}>
                            <h1>  :     /       !</h1>
                            <img className="scale" src={Arrow} alt="" style={{ position: 'absolute', top: '33%', marginLeft: '20%' }}/></Div></div>
                   
                    <div style={{ position: 'relative', marginTop: '20vh' }}>
                            <Div style={{ textAlign: 'center', fontSize: 18, fontFamily: 'Neucha' }}>
                                <h1>          !</h1>
                            <Button style={{ position: 'relative', marginTop: '19vh' }} onClick={() => firstOpenApp()} mode='outline' size='xl'> <h1 style={{ textAlign: 'center', fontSize: 40, fontFamily: 'Neucha' }}>!</h1></Button></Div></div>

                </Gallery>
            </div>
        </Panel>
    )
};

export default Intro;