import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Panel, PanelHeader, PanelHeaderBack, Button, Progress, Div} from '@vkontakte/vkui/';
import Icon28PlaceOutline from '@vkontakte/icons/dist/28/place_outline';
import './Style.css';

const Play = (props) => {
	const [leftCity, setStateLeftCity] = useState(" ");
	const [rightCity, setStateRightCity] = useState(" ");
	const [count, setStateCount] = useState(100);
	const [timer, setStateTimer] = useState();

	const [leftCityDictance, setStateleftCityDictance] = useState(" ");
	const [rightCityDictance, setStaterightCityDictance] = useState(" ");
	const [leftCityResponse, setStateleftCityResponse] = useState(0);
	const [rightCityResponse, setStaterightCityResponse] = useState(0);
	const [statusBackground, setStatestatusBackground] = useState("stage");
	const [isStartGame, setStateisStartGame] = useState(false)
	const [isOverGame, setStateisOverGame] = useState(false)
	const [isNextGame, setStateisNextGame] = useState(false)
	const [timerId, setStatetimerId] = useState("timer")
	const [progressId, setStateprogressId] = useState("progressNone")
	const [resultId, setStateresultId] = useState("result")
	const [result, setStateresult] = useState()

	const [userPostRes, setStateuserPostRes] = useState(false)
	const [geoUser, setStateGeoUser] = useState(<br />);
	const [leftButton, setStateLeftButton] = useState(props.left);
	const [rightButton, setStateRightButton] = useState(props.right);

	const [statusRightButton, setStateStatusRightButton] = useState('cityButtonRight')
	const [statusLeftButton, setStateStatusLeftButton] = useState('cityButtonLeft')

	useEffect(() => {
		startGame()
		if (isNextGame === false) {
			setStateisNextGame(true)
		} else {

		}
	}, []);

	const nextGame = () => {

		

		if (isNextGame === false) {
			setStateisNextGame(true)
			startGame()
		} else {

		}

	}
	

	const startGame = () => {		
		setStateStatusLeftButton('cityButtonLeft')
		setStateStatusRightButton('cityButtonRight')
		setStateTimer()
		setStatetimerId("timer")
		setStateprogressId("progress")
		setStateresultId("resultNone")
		setStatestatusBackground("stage")
		setStateisOverGame(false)
		setStateisStartGame(false)
		setStaterightCityDictance();
		setStateleftCityDictance();
		setStateTimer();
		setStateGeoUser(props.geolocation)


		const timerId = setInterval(() => {


			var randRight = Math.floor(Math.random() * rightButton.length);
			var randLeft = Math.floor(Math.random() * leftButton.length);
			let arrLeft = Object.fromEntries(leftButton[randLeft]);
			let arrRight = Object.fromEntries(rightButton[randRight]);
			setStateRightCity(arrRight.city)
			setStateLeftCity(arrLeft.city)

		}, 30);

		setTimeout(() => { clearInterval(timerId); countDown(); setStateisStartGame(true) }, 3000);

	}

	const countDown = () => {
		var randRightCity = Math.floor(Math.random() * rightButton.length);
		let arrRight = Object.fromEntries(rightButton[randRightCity]);

		var randLeftCity = Math.floor(Math.random() * leftButton.length);
		let arrLeft = Object.fromEntries(leftButton[randLeftCity]);

		if (randRightCity === randLeftCity) {
			var randRightCity = Math.floor(Math.random() * rightButton.length);
			let arrRight = Object.fromEntries(rightButton[randRightCity]);

			var randLeftCity = Math.floor(Math.random() * leftButton.length);
			let arrLeft = Object.fromEntries(leftButton[randLeftCity]);
			setStateRightCity(arrRight.city)
			setStateLeftCity(arrLeft.city)
		} else {
			
		}


		

		setStateRightCity(arrRight.city)
		setStateLeftCity(arrLeft.city)

		function getDistanceFromLatLonInKmRight(lat4, lon4, lat8, lon8) {
			var R1 = 6371;
			var dLat1 = deg2rad(lat8 - lat4);
			var dLon1 = deg2rad(lon8 - lon4);
			var a1 =
				Math.sin(dLat1 / 2) * Math.sin(dLat1 / 2) +
				Math.cos(deg2rad(lat4)) * Math.cos(deg2rad(lat8)) *
				Math.sin(dLon1 / 2) * Math.sin(dLon1 / 2)
				;
			var c1 = 2 * Math.atan2(Math.sqrt(a1), Math.sqrt(1 - a1));
			var d1 = R1 * c1;
			return d1;
		}
		function deg2rad(deg1) {
			return deg1 * (Math.PI / 180)
		}
		setStaterightCityResponse(+getDistanceFromLatLonInKmRight(props.position.split(',')[0], props.position.split(',')[1], arrRight.lat, arrRight.long).toFixed(0))
		function getDistanceFromLatLonInKmLeft(lat1, lon1, lat2, lon2) {
			var R = 6371;
			var dLat = deg2rad(lat2 - lat1);
			var dLon = deg2rad(lon2 - lon1);
			var a =
				Math.sin(dLat / 2) * Math.sin(dLat / 2) +
				Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) *
				Math.sin(dLon / 2) * Math.sin(dLon / 2)
				;
			var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
			var d = R * c;
			return d;
		}

		function deg2rad(deg) {
			return deg * (Math.PI / 180)
		}
		setStateleftCityResponse(+getDistanceFromLatLonInKmLeft(props.position.split(',')[0], props.position.split(',')[1], arrLeft.lat, arrLeft.long).toFixed(0))

		let i = 100
		let t = 4
		let timerIdcountDown = setInterval(() => {

			setStateTimer(" " + t-- + " ")

			setStateCount(i = i - 20)

		}, 1000);

		setTimeout(() => {
			setStateTimer(" !")

			setStateisOverGame(true)
			setStateisNextGame(false)
			clearInterval(timerIdcountDown);
			setStateCount(100);
			setStaterightCityDictance(getDistanceFromLatLonInKmRight(props.position.split(',')[0], props.position.split(',')[1], arrRight.lat, arrRight.long).toFixed(0) + " ");
			setStateleftCityDictance(getDistanceFromLatLonInKmLeft(props.position.split(',')[0], props.position.split(',')[1], arrLeft.lat, arrLeft.long).toFixed(0) + " ");
		}, 4500);
	}


	const userResponseLeft = () => {

	
		
		
		if (isOverGame === false) {
			if (isStartGame === true) {
				if (leftCityResponse < rightCityResponse) {
					
					bridge.send("VKWebAppStorageGet", { "keys": ["rating"] })
						.then(data => {
								bridge.send("VKWebAppStorageSet", { "key": "rating", "value": (leftCityResponse + parseInt(data.keys[0].value)) + ' ' });
						})

					let timerIdcountDown = setInterval(() => {
						setStaterightCityDictance(Math.floor(Math.random() * 15000) + " ");
						setStateleftCityDictance(Math.floor(Math.random() * 15000) + " ");
					}, 30);

					setTimeout(() => {
						 
						clearInterval(timerIdcountDown);
						setStaterightCityDictance((rightCityResponse).toFixed(0) + " ");
						setStateleftCityDictance((leftCityResponse).toFixed(0) + " ");
						setStateresult(<h1 style={{ color: 'white', fontSize: 30, fontFamily: 'Neucha', textAlign: "center" }}>  !</h1>)
						setStateresultId("result")
						setStateStatusLeftButton('cityButtonLeftSucces')

					}, 2000);
					
				} else {
					let timerIdcountDown = setInterval(() => {
						setStaterightCityDictance(Math.floor(Math.random() * 15000) + " ");
						setStateleftCityDictance(Math.floor(Math.random() * 15000) + " ");
					}, 30);

					setTimeout(() => {
						clearInterval(timerIdcountDown);
						setStaterightCityDictance((rightCityResponse).toFixed(0) + " ");
						setStateleftCityDictance((leftCityResponse).toFixed(0) + " ");
						setStateresult(<h1 style={{ color: 'white', fontSize: 30, fontFamily: 'Neucha', textAlign: "center" }}> !</h1>)		
						setStateresultId("result")
						setStateStatusLeftButton('cityButtonLeftError')
					}, 2000);
					

				}
				
				
				setStateisOverGame(true)
				setStateuserPostRes(true)
				setStatetimerId("timerNone")
				setStateprogressId("progressNone")
			} else {

			}
		} else {

		}



	}
	const userResponseRight = () => {


		
		
		if (isOverGame === false) {
			if (isStartGame === true) {
				if (leftCityResponse > rightCityResponse) {
					bridge.send("VKWebAppStorageGet", { "keys": ["rating"] })
						.then(data => {
				
							bridge.send("VKWebAppStorageSet", { "key": "rating", "value": (rightCityResponse + parseInt(data.keys[0].value)) + ' ' });

						})
					let timerIdcountDown = setInterval(() => {
						setStaterightCityDictance(Math.floor(Math.random() * 15000) + " ");
						setStateleftCityDictance(Math.floor(Math.random() * 15000) + " ");
					}, 30);

					setTimeout(() => {
						clearInterval(timerIdcountDown);
						setStaterightCityDictance((rightCityResponse).toFixed(0) + " ");
						setStateleftCityDictance((leftCityResponse).toFixed(0) + " ");
						setStateresult(<h1 style={{ color: 'white', fontSize: 30, fontFamily: 'Neucha', textAlign: "center" }}>  !</h1>)
						setStateresultId("result")
						setStateStatusRightButton('cityButtonRightSucces')

					}, 2000);

				} else {
					let timerIdcountDown = setInterval(() => {
						setStaterightCityDictance(Math.floor(Math.random() * 15000) + " ");
						setStateleftCityDictance(Math.floor(Math.random() * 15000) + " ");
					}, 30);

					setTimeout(() => {
						clearInterval(timerIdcountDown);
						setStaterightCityDictance((rightCityResponse).toFixed(0) + " ");
						setStateleftCityDictance((leftCityResponse).toFixed(0) + " ");
						setStateresult(<h1 style={{ color: 'white', fontSize: 30, fontFamily: 'Neucha', textAlign: "center"}}> !</h1>)
						setStateresultId("result")
						setStateStatusRightButton('cityButtonLeftError')
					}, 2000);


				}
				
				setStateisOverGame(true)
				setStateuserPostRes(true)
				setStatetimerId("timerNone")
				setStateprogressId("progressNone")

			} else {

			}
		} else {

		}


	}


	return (
		<Panel id={props.id} separator={false}>
			<PanelHeader left={<PanelHeaderBack onClick={() => window.history.back()} />}>
			</PanelHeader>
			<div className='stage'>
				<ul class="circles">
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>

				</ul>


				<div className="topBlock">
					<div className="titlePlay" style={{ color: "white", fontSize: 25, fontFamily: 'Neucha' }}>
						{props.title}</div>
					<Icon28PlaceOutline className="iconPlace" width={25} height={25} />
					<div className="earth"></div>
					<Div className="userCityName"><h1 style={{ color: "white", fontSize: 15, fontFamily: 'Neucha', textAlign: "center" }}>{geoUser}</h1></Div>
				</div>
				<div className="blockCities">
					<div className="cityDivLeft"><Button className={statusLeftButton} onClick={userResponseLeft} size="l" mode="outline" style={{ color: "white", fontFamily: 'Neucha', textAlign: "center" }}><h3>{leftCity}<br />{leftCityDictance}</h3></Button></div>
					<div className="cityDivRight"><Button className={statusRightButton} onClick={userResponseRight} size="l" mode="outline" style={{ color: "white", fontFamily: 'Neucha', textAlign: "center" }}><h3>{rightCity}<br />{rightCityDictance}</h3></Button></div>
				</div>


				<div className="blockProgress">
					<div className="progressItems">
						<div className={resultId}>{result}</div>

						<div className={progressId}><Progress style={{ background: "red" }} value={count} />
							<div className={timerId}><h1 style={{ color: "white", fontSize: 30, fontFamily: 'Neucha', textAlign: "center" }}>{timer}</h1></div>
						</div>
						<div className="nextDivLevel"><Button className="nextButtonLevel" onClick={nextGame} size="l" mode="outline" ><h1 style={{ color: "white", fontFamily: 'Neucha', textAlign: "center", fontSize: 27 }}></h1></Button></div>
					</div>


				</div>


			</div>
		</Panel>
	)

};

export default Play;