const App = () => {

	const [activePanel, setActivePanel] = useState("Intro"); 
	const [history, setHistory] = useState(['Home']);
	const [scheme, SetStateScheme] = useState('space_gray'); 
	const [position, setStatePosition] = useState();
	const [geolocation, setStateGeolocation] = useState();
	const [left, setStateLeft] = useState(arrCityLeft)
	const [right, setStateRight] = useState(arrCityRight)
	const [chooseLevel, setStateChooseLevel] = useState('')
	const [descriptionButton, setStateDescriptionButton] = useState('    .')
	const [title, setStateTitle] = useState(<h1>  <br/> ?</h1>)


	function goToPage(name) {
		window.history.pushState({ panel: name }, name); 
		setActivePanel(name); 
		history.push(name);
	};

	const goBack = () => {
		if (history.length === 1) {  
			bridge.send("VKWebAppClose", { "status": "success" }); 
		} else if (history.length > 1) {
			history.pop() 
			setActivePanel(history[history.length - 1]) 
		}
	}

	const goToHome = () => {
		setActivePanel('Home')
	}

	function camelCase(scheme, needChange = false) {
		let isLight = lights.includes(scheme);

		if (needChange) {
			isLight = !isLight;
		}
		SetStateScheme(isLight ? 'bright_light' : 'space_gray');

		bridge.send('VKWebAppSetViewSettings', {
			'status_bar_style': isLight ? 'dark' : 'light',
			'action_bar_color': isLight ? '#ffffff' : '#191919'
		});
	}

	useEffect(() => {
		
		
		bridge.send("VKWebAppStorageGet", { "keys": ["geo", "pos", "hasSeenOnboarding7"] })
			.then(data => {
				setStatePosition(data.keys[1].value)
				setStateGeolocation(data.keys[0].value)
				if (data.keys[2].value === 'true') {
					setActivePanel('Home')
				} else {
					setActivePanel('Intro')
				}
			})
			.catch(e=>{
				console.log(e);
			})
		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				
			}
		});
		window.addEventListener('popstate', () => goBack()); 

	}, []);

	const savePositionGlobal = (pos, geo) => {
		setStatePosition(pos)
		setStateGeolocation(geo)
		bridge.send("VKWebAppStorageSet", { "key": "pos", "value": pos });
		bridge.send("VKWebAppStorageSet", { "key": "geo", "value": geo });
	}


	const [iconStatus, setStateIconStatus] = useState(<Icon24Fullscreen width={40} height={40}/>)

	const changeLevel = () => {


		if (right === arrCityRight) {
			setStateRight(arrCountryRight)
			setStateLeft(arrCountryLeft)
			setStateChooseLevel('')
			setStateDescriptionButton('    .')
			setStateTitle(<h1>  <br /> ?</h1>)
			setStateIconStatus(<Icon24FullscreenExit width={40} height={40}/>)
		}
		if (right === arrCountryRight ) {
			setStateRight(arrCityRight)
			setStateLeft(arrCityLeft)
			setStateChooseLevel('')
			setStateDescriptionButton('    .')
			setStateTitle(<h1>  <br/> ?</h1>)
			setStateIconStatus(<Icon24Fullscreen width={40} height={40}/>)

		}
		

		}


	

	return  (

		<ConfigProvider isWebView={true} scheme={scheme}>
			<View 
				activePanel={activePanel} 
				history={history} 
				onSwipeBack={goBack} 
			>
				<Loader id="Loader"
					scheme={scheme}
					camelCase={camelCase}
					goToPage={goToPage}
				/>
				<Intro id="Intro"
					scheme={scheme}
					camelCase={camelCase}
					goToPage={goToPage}
					goToHome={goToHome}
				/>
				<Home id="Home"
					scheme={scheme}
					camelCase={camelCase}
					goToPage={goToPage}
					position={position}
					geolocation={geolocation}
					chooseLevel={chooseLevel}
					iconStatus={iconStatus}
					changeLevel={changeLevel}


				/>
				<Play
					id="Play"
					scheme={scheme}
					camelCase={camelCase}
					goToPage={goToPage}
					position={position}
					geolocation={geolocation}
					left={left}
					right={right}
					title={title}

					


				/>
				<Settings
					id="Settings"
					scheme={scheme}
					camelCase={camelCase}
					savePositionGlobal={savePositionGlobal}
					changeLevel={changeLevel}
					geolocation={geolocation}
					chooseLevel={chooseLevel}
					descriptionButton={descriptionButton}
					iconStatus={iconStatus}
				/>
				<Rating
					id="Rating"
				/>
				<Info
					id="Info"
					scheme={scheme}
					camelCase={camelCase}
					goToPage={goToPage}
				/>
				<Warning
					id="Warning"
					scheme={scheme}
					camelCase={camelCase}
					goToPage={goToPage}
					savePositionGlobal={savePositionGlobal}
					geolocation={geolocation}
				/>
			</View>
		</ConfigProvider>
		
		
	);
}

export default App;