import React from 'react';
import {Panel, Button} from '@vkontakte/vkui/';
import Icon48Play from '@vkontakte/icons/dist/48/play';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import Icon28ErrorOutline from '@vkontakte/icons/dist/28/error_outline';
import Icon28GraphOutline from '@vkontakte/icons/dist/28/graph_outline';
import './Style.css';

const Home = (props) => {

	
	const startGame = () => {

		if (props.position === undefined || props.position === '') {

			props.goToPage('Warning')
			
		} else {

			props.goToPage('Play')
			
		}
	}

	const changeLevel = () => {
		props.changeLevel()
	}


	return ( 
		<Panel id={props.id} separator={false}>
			<div className="stage">
				<ul class="circles">
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>	
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					
				</ul>
				<div className="title"><h1 style={{ color: "white", fontSize: 45, fontFamily: 'Neucha' }}>Towner</h1></div>
				<Button className="startButton" before={<Icon48Play style={{ color: "white" }} />} mode="outline" size="xl" onClick={() => startGame()}>
					<p style={{
						color: "white",
						fontSize: 30,
						fontFamily: 'Neucha',
					}}>!</p>
				</Button>
				<Button className="levelButton" before={props.iconStatus} onClick={() => changeLevel()} size='xl' mode='outline'><h1 style={{ color: "white", fontFamily: 'Neucha', fontSize: 19 }}>{props.chooseLevel}</h1></Button>

				<div className="blockButton">
				<Button
					className="settingsButton"
					before={<Icon28SettingsOutline width={41} height={41} style={{ color: "white" }} />}
					mode="outline"
					size=""
					onClick={() => props.goToPage('Settings')}
				/>
				<Button
					className="ratingButton"
						before={<Icon28GraphOutline width={41} height={41} style={{ color: "white" }} />}
					mode="outline"
					size=""
					onClick={() => props.goToPage('Rating')}
				>
					<p style={{
						color: "white",
						fontSize: 30,
						fontFamily: 'Neucha',
					}}></p>
				</Button>
				<Button
					className="aboutButton"
					before={<Icon28ErrorOutline width={41} height={41} style={{ color: "white" }} />}
					mode="outline"
					data-to="persik"
					size=""
					onClick={() => props.goToPage('Info')}
				>
					<p style={{
						color: "white",
						fontSize: 30,
						fontFamily: 'Neucha',
					}}></p>
				</Button>
				</div>
			</div>
			</Panel>
)};

export default Home;
