import React, { useEffect, useState } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Panel, PanelHeader, Separator, Div, PanelHeaderBack, Cell, Button, Avatar, Group, Header} from '@vkontakte/vkui/';
import Icon28ShareOutline from '@vkontakte/icons/dist/28/share_outline';

const Rating = (props) => {

	const [photo, setStatePhoto] = useState()
	const [userName, setStateUserName] = useState()
	const [rating, setStateRating] = useState(0)

	useEffect(() => {
		bridge.send("VKWebAppStorageGet", { "keys": ["rating"] })
			.then(data => {
				console.log(data.keys[0].value);

				if (data.keys[0].value === '') {
					setStateRating(0 + ' ')
				}
				else {
					setStateRating(data.keys[0].value)
				}
			})

		bridge.send("VKWebAppGetUserInfo")
			.then(data => {
				setStatePhoto(data.photo_200)
				setStateUserName(data.first_name + ' ' + data.last_name)
			})

	}, [])


	return (

		<Panel id={props.id} separator={false}>
			<PanelHeader left={<PanelHeaderBack onClick={() => window.history.back()} />}>
				
        </PanelHeader>
			<div className='stage'>
				<ul class="circles">
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>

				</ul>
				<div style={{ position: 'relative', marginTop: '18vh' }}>
					<Group header={
						<Header> </Header>}>

						<Cell before={<Avatar mode="app" src={photo} />} description={': ' + `${rating}`} multiline={true}>
							{userName}
						</Cell>
						<Separator/>
						<Div>
							<Button mode='outline' before={<Icon28ShareOutline style={{ color: "white" }} />} size='xl' onClick={() => bridge.send("VKWebAppShare", { "link": "https://vk.com/app7612376" })}> </Button>
						</Div>
					</Group>
				</div>
			</div>
		</Panel>
	)
};

export default Rating;





